<script>
    import PageIndex from "../core/page-index";
    export default {
        name: "user-view",
        components: {PageIndex},
        data() {
            return {
                user: {
                },
                loading: true
            };
        },
        methods : {
            action(action)
            {
                window.action(action , 'Agent' , `${window.location.origin}/admin/portal/agents/${this.$route.params.id}/${action}`).then((response) => {
                    this.user = response.data.body.model;
                });
            },
            init(){
                this.loading = true;
                window.axios.get(`${window.location.origin}/admin/portal/agents/${this.$route.params.id}/view`).then((response) => {
                    this.user = response.data.body.model;
                }).finally(() => {
                    this.loading = false;
                });
            }
        },
        mounted() {
            this.init();
        }
    }
</script>
<template>
    <page-index :url="`/admin/portal/agents/${$route.params.id}/transactions`" prefix="list" name="Transactions">
        <template slot-scope="data" slot="filters">
            <div class="d-flex row align-items-center form-group mr-3">
                <div class="mr-3 d-flex align-items-center">
                    <select type="text" v-model="data.filters['state']" class="custom-select">
                        <option :value="undefined">Choose State</option>
                        <option selected value="COMPLETED">COMPLETED</option>
                        <option value="PENDING-AUTHORISATION">PENDING AUTHORISATION</option>
                        <option value="PAYMENT-COMPLETED">PAYMENT COMPLETED</option>
                        <option value="PAYMENT-FAILED">PAYMENT FAILED</option>
                        <option value="BILL-ABORTED">BILL ABORTED</option>
                        <option value="BILL-FAILED">BILL FAILED</option>
                    </select>
                </div>
                <div class="d-flex align-items-center mr-1">
                    <div class="app-search flex-fill">
                        <form>
                            <div class="input-group"><input type="date" v-model="data.filters['startDate']" class="form-control bg-white border">
                                <span class="mdi mdi mdi-calendar-arrow-right"></span>
                                <div class="input-group-append">
                                    <button type="submit" disabled :class="['btn' ,'btn-primary shadow-none']">Start Date</button>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
                <div class="d-flex align-items-center">
                    <div class="app-search flex-fill">
                        <form>
                            <div class="input-group"><input type="date" v-model="data.filters['endDate']" class="form-control bg-white border">
                                <span class="mdi mdi mdi-calendar-arrow-right"></span>
                                <div class="input-group-append">
                                    <button type="submit" disabled :class="['btn' ,'btn-primary shadow-none']">End Date</button>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </template>
        <template slot="sort-fields">
            <option value="created_at">Created</option>
            <option value="updated_at">Updated</option>
        </template>
        <template slot="table-header">
            <th></th>
            <th>ID</th>
            <th>ID</th>
            <th>Amount</th>
            <th>Info</th>
            <th>Info</th>
            <th>Date</th>
            <th class="text-center"></th>
        </template>
        <template slot="table-row" slot-scope="data">
            <td>
                <i :class="['mdi mdi-square' , data.row.state === 'COMPLETED' ? 'text-success' : 'text-danger' ]"></i>
            </td>
            <td>
                <span class="text-primary">#{{ data.row.id }}</span>
            </td>
            <td>
                <span class="text-primary">  <router-link class="btn btn-sm btn-light px-3 py-1 mb-1 font-12" :to="`/admin/${data.row.p_type}/${data.row.payment_id}/view`"> <strong>{{ data.row.p_type | model }} #{{ data.row.payment_id }}</strong></router-link></span> <br>
                <span class="text-primary">  <router-link class="btn btn-sm btn-light px-3 py-1 mb-1 font-12" :to="`/admin/${data.row.t_type}/${data.row.transaction_id}/view`"> <strong>{{ data.row.t_type | model }} #{{ data.row.transaction_id }}</strong></router-link></span><br>
            </td>
            <td>
                <div class="">Amount : <strong>{{ data.row.amount }}</strong></div>
                <div class="">Currency : <strong>{{ data.row.currency }}</strong> : Rate :  <strong> {{ data.row.rate }} </strong> </div>
                <div class="">Payment Completed : <i :class="['mdi mdi-square' , data.row.payment_completed ? 'text-success' : 'text-danger' ]"></i>  </div>
                <div class="">Email : <strong>{{ data.row.email }}</strong></div>
            </td>
            <td>
                <div class="">Type : <strong>{{ data.row.t_type }}</strong></div>
                <div class="">Payment : <strong>{{ data.row.p_type }}</strong></div>
                <div class="">Channel : <strong>{{ data.row.channel }}</strong></div>
                <div class="">Phone : <strong>{{ data.row.phone }}</strong></div>
            </td>
            <td>
                <div class="">State : <strong>{{ data.row.state }}</strong></div>
                <div class="">Error : <strong>{{ data.row.error | string_limit }}</strong></div>
                <div class="">Ref : <strong>{{ data.row.ref }}</strong></div>
                <div class="">Payment Ref : <strong>{{ data.row.payment_ref }}</strong></div>
            </td>
            <td>
                Scope : <strong> {{ data.row.scope }} </strong><br>
                Created : {{ data.row.created_at }} <br>
                Updated : {{ data.row.updated_at }}
            </td>
            <td>
                <router-link :to="`/admin/transactions/${data.row.id}/view`" class="action-icon text-primary">
                    <i class="mdi mdi-eye mdi-24px"/>
                </router-link>
            </td>
        </template>
    </page-index>
</template>
